@media screen and (max-width: 700px) {
    :root {
        /* PersonalInformation.css */
        --personal-col: 100% !important;
        --pgrid-row: 2;
        --personal-height: 350px !important;
        /* App.css */
        --app-col: 100% !important;
        --app-rows: repeat(12, 280px) !important;
        --header-fsize: 43px !important;
        
        --dark-main: #49c487ae;
        --main-color: #49C486;
        --mainbackground-color: #2a2a2e;
        --highlight-blue: #3C45B1;

        --cv-gr: 2/20;
        /* ffa73b */
        /* --dark-main: #ffa73bd8;
        --main-color: #ffa73b; */
    }

    /* PersonalInformation.css */
    .form-head {
        height: 850px !important;
        grid-row: 3;
        align-self: center;
        grid-template-columns: 100% !important;
    }
    
    .form-head > * {
        grid-column: 1/2 !important;
        width: 100% !important;
        align-self: center !important;
    }

    .form-head > h1 {
        font-size: 30px !important;
        grid-row: 1;
        top: -40px;
    }
    
    .form-head div:nth-child(2) { grid-row: 2 !important; }
    .form-head div:nth-child(3) { grid-row: 4 !important; } 
    .form-head div:nth-child(4) { grid-row: 6 !important; }
    .form-head div:nth-child(5) { grid-row: 8 !important; }
    .form-head div:nth-child(6) { grid-row: 10 !important; }
    .form-head div:nth-child(7) { grid-row: 12 !important; }
    .form-head div:nth-child(8) { grid-row: 14 !important; }
    .form-head div:nth-child(9) { grid-row: 16 !important; }

    /* Experience.css */
    .Experience {
        grid-template-columns: 100%;
        grid-row: 9;
        height: 850px;
    }

    .Experience > * {
        grid-column: 1/2 !important;
        width: 100% !important;
        align-self: center !important;
        justify-self: center !important;
    }
    
    .Experience > h1 {
        font-size: 30px !important;
        top: -45px;
    }

    .Experience div:nth-child(2) { grid-row: 2 !important;}
    .Experience div:nth-child(3) { grid-row: 4 !important;}
    .Experience div:nth-child(4) { grid-row: 6 !important;}
    .Experience div:nth-child(5) { grid-row: 8 !important;}
    .Experience div:nth-child(6) { grid-row: 10 !important;}
    .Experience div:nth-child(7) { grid-row: 12 !important;}
    .Experience div:nth-child(8) { grid-row: 15 !important;}
    .Experience button:nth-child(9) { grid-row: 17 !important;}
    .Experience button:nth-child(10) { grid-row: 19 !important; margin: 0 !important;}
    

    /* Education.css */
    .EducationContainer {
        grid-template-columns: 100% !important;
        grid-template-rows: repeat(auto-fit, 1fr);
        grid-row: 13 !important;
        height: 850px;
    }

    .EducationContainer > h1 {
        font-size: 30px !important;
        top: -45px;
    }

    .EducationContainer > * {
        grid-column: 1/2 !important;
        width: 100% !important;
        align-self: center !important;
        justify-self: center !important;
    }

    .EducationContainer div:nth-child(2) { grid-row: 2 !important;}
    .EducationContainer div:nth-child(3) { grid-row: 4 !important;}
    .EducationContainer div:nth-child(4) { grid-row: 6 !important;}
    .EducationContainer div:nth-child(5) { grid-row: 8 !important;}
    .EducationContainer div:nth-child(6) { grid-row: 10 !important;}
    .EducationContainer div:nth-child(7) { grid-row: 12 !important;}
    .EducationContainer div:nth-child(8) { grid-row: 15 !important;}
    .EducationContainer button:nth-child(9) { grid-row: 17 !important;}
    .EducationContainer button:nth-child(10) { grid-row: 19 !important; margin: 0 !important;}



    .appContainer > div {
        grid-column: 1/2 !important;
        justify-content: center;
    }

    .form-head { 
        grid-template-columns: 100% !important; 
        grid-template-rows: repeat(auto-fit, 1fr) !important;
    }

    /* finalize */
    .finalizeForm {
        top: -50px !important;
        grid-column: 1/2 !important;
        grid-row: 19/20 !important;
        align-self: center !important;
    }

    .buttonContainer {
        position: relative;
        top: -50px !important;
        height: 68% !important;
        align-self: flex-end;
        grid-row: 19/21 !important;
        flex-direction: column;
        grid-gap: 25px !important;
    }

    .buttonContainer button:nth-child(1) { 
        display: none;
        position: relative;
        background-color: #49c486 !important;
        color: black !important;
        width: 100% !important;
        align-self: center !important;
        top: 30px !important;
    }
    .buttonContainer button:nth-child(2) {
        position: relative;
        width: 100% !important;
        align-self: center !important;
        top: 55px !important;
    }
    
}

@media screen and (max-width: 950px) {
    .cv-container { display: none !important; }
    button { font-size: 30px !important; }
    :root {
        /* PersonalInformation.css */
        --personal-rows: repeat(20, 5%);
        --personal-col: repeat(20, 5%) !important;
        --personal-height: 350px;
        
        /* App.css */
        --app-rows: repeat(6, 300px) 0px 0px 0px 0px repeat(8, minmax(100px, 1fr)) 100px;
        --app-col: repeat(20, 5%);
        --header-fsize: 60px;

        --dark-main: #49c487ae;
        --main-color: #49C486;
        
        --mainbackground-color: #2a2a2e;
        --highlight-blue: #3C45B1;
        
        --cv-gr: 2/20;
        /* ffa73b */
        /* --dark-main: #ffa73bd8;
        --main-color: #ffa73b; */
    }
}

@media all and (min-width: 949px) {
    :root {
        /* PersonalInformation.css */
        --personal-rows: repeat(20, 5%);
        --personal-col: repeat(20, 5%) !important;
        --personal-height: 350px;

        /* App.css */
        --app-rows: 350px repeat(12, 200px) !important;        
        --app-col: repeat(20, 5%);
        --header-fsize: 60px;

        --dark-main: #49c487ae;
        --main-color: #49C486;
        
        --mainbackground-color: #2a2a2e;
        --highlight-blue: #3C45B1;
        --cv-gr: 2/10;
        /* ffa73b */
        /* --dark-main: #ffa73bd8;
        --main-color: #ffa73b; */
    }
}

@media all and (max-width: 945px) {
    :root {

        --app-rows: 350px repeat(12, 133px);
  }
}

