.cv-container {
    display: grid;
    border: 15px solid var(--main-color);
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: 200px repeat(18, 1fr) 200px;
    background-color:  rgba(245, 245, 245, 0.702)!important;
    width: 100%;
    height: 1fr;
    min-height: 1500px;
    max-width: 2000px;
    grid-column: 1/21 !important;
    grid-row: 11/19 !important;
}

@media all {
    .page-break {
      display: none;
    }
  }
  
  @media print {
    html, body {
      height: initial !important;
      overflow: initial !important;
      -webkit-print-color-adjust: exact;
    }
  }
  
  @media print {
    .page-break {
      margin-top: 1rem;
      display: block;
      page-break-before: auto;
    }
  }
  
  @page {
    size: auto;
    margin: 20mm;
  }


.cv-preview {
    display: grid;
    /* background-color: rgba(245, 245, 245, 0.518); */
    background-color: #a6a6a7;
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: repeat(auto-fit, 1fr) !important;
    grid-area: 
    "head"
    "side"
    "main";
    justify-self: center !important;
    max-width: 1500px !important;
    width: 80vw;
    grid-row: 2/20;
    grid-column: 1/21;
    justify-self: center;
    height: 1fr;
}

.cv-head {
    display: grid;
    grid-area: "head";
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: repeat(20, 5%);
    background-color: #2a2a2e;
    height: 100%;
    min-height: 270px !important;
    width: 100%;
    grid-column: 1/21;
    grid-row: 1/2;
}

.cv-head-h1 {
    font-size: 80px;
    top: 20px !important;
    grid-column: 1/21 !important;
    grid-row: 6 !important;
    justify-content: center;
}

.cv-head-h2 {
    font-size: 40px;
    font-weight: 500;
    color: var(--main-color);
    grid-column: 1/21;
    grid-row: 9;
}

.cv-head-contact {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    text-orientation: sideways-right;
    writing-mode: vertical-rl;    
    font-size: 25px;
    color: whitesmoke;
    font-weight: 100;
    grid-column: 19/20;
    grid-row: 1/21;
}

.cv-side {
    grid-area: "side";
    display: grid;
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: repeat(20, 5%);
    background-color: #a6a6a7;
    grid-column: 1/8;
    grid-row: 3/21 !important;
}

.side-details {
    display: grid;
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: repeat(20, 5%);
    grid-column: 2/20;
    grid-row: 1;
    height: 300px;
}

.side-header {
    display: flex;
    grid-column: 1/21;
    grid-row: 1;
    justify-content: center;
    align-content: flex-end;
    border-bottom: 2px solid #2a2a2e;
    height: 50px;
}

.side-main {
    display: grid;
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: repeat(auto-fit, 100px);
    grid-column: 1/21;
    grid-row: 4;
    height: 100%;
}

.details-address {
    grid-column: 1/21;
    grid-row: 1/10;
    font-size: 25px;
    font-weight: 500;
    overflow-wrap: break-word !important;
}

.details-phone {
    grid-column: 1/21;
    grid-row: 9/13;
    align-content: center !important;
    font-size: 25px;
    font-weight: 500;
    overflow-wrap: break-word !important;
}

.details-email {
    width: 100%;
    height: 100%;
    overflow-wrap: break-word !important;
    grid-column: 1/21;
    grid-row: 14/21;
    font-size: 25px;
    font-weight: 500;
}

.page-break {
    display: grid;
    grid-area: "main";
    grid-template-columns: repeat(20, 5%);
    grid-template-areas: 
                "exp";
    grid-column: 8/21;
    grid-row: 2/21 !important;
    background-color: rgba(245, 245, 245, 0.518);
}

.cv-main-experience {
    display: grid;
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: repeat(20, 5%);
    grid-column: 1/21;
    height: 100px !important;

}

.p1 {
    display: grid;
    grid-column: 2/20;
    grid-row: 7 !important;
    width: 100%;
    height: 100%;
    justify-content: flex-start;
}

.p2 {
    width: 100%;
    height: 100%;
    display: grid;
    justify-content: flex-start;
    grid-column: 2/20;
    grid-row: 11;
}

.p3 {
    width: 100%;
    height: 100%;
    display: grid;
    justify-content: flex-start;
    grid-column: 2/20;
    grid-row: 15;
}

.cv-main-experience > h2 {
    grid-column: 2/21;
    text-align: left;
    width: 100%;
}

.cv-main-education {
    grid-column: 1/21;
    display: grid;
    grid-template-columns: repeat(20, 5%);
    grid-template-rows: repeat(20, 5%);
    height: 100px !important;
}

.educationh2 {
    text-align: left;
    border-bottom: 2px solid #2a2a2e;
}

.eduH2 {
    font-size: 25px;
    font-weight: 800;
    text-align: left;
    grid-column: 2/21 !important;
    grid-row: 1;
}

.eduP1 {
    text-align: left;
    grid-column: 2/21;
    grid-row: 6;
}

.eduP2 {
    text-align: left;
    grid-column: 2/21;
    grid-row: 9;
}

.eduP3 {
    text-align: left;
    grid-column: 2/21;
    grid-row: 12;
}

.eduP4 {
    text-align: left;
    grid-column: 2/21;
    grid-row: 15;
}

.cv-preview-h1 {
    color: black !important;
}
